/* Header component styles */

.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  background-color: var(--color-background);
  border-bottom: 1px solid var(--color-border);
  box-shadow: 0 2px 10px var(--color-shadow);
  z-index: 11001; /* higher than mobile-menu(10000) and backdrop(9998) */
  /* capatalize the header text */
  text-transform: uppercase;
  transition:
    background-color var(--transition-medium),
    border-color var(--transition-medium),
    box-shadow var(--transition-medium);
}

.header__container {
  max-width: none; /* stretch across the entire screen */
  width: 100%;
  margin: 0;
  padding: 0.75rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  cursor: pointer;
  transition: transform var(--transition-fast);
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.header__logo:hover {
  transform: scale(1.05);
}

.header__logo-image {
  height: 60px;
  width: auto;
  max-width: 200px;
  object-fit: contain;
}

.header__logo-text {
  font-size: 2.5rem;
  font-weight: bold;
  font-family: 'Libre Baskerville', serif;
  color: var(--color-text);
  transition: color var(--transition-fast);
  white-space: nowrap;
}
.header__logo-text-zen {
  color: var(--color-tertiary);
}

.header__flag {
  font-size: 1.25rem;
  line-height: 1;
  margin-left: 0.25rem;
}

.header__logo:hover .header__logo-text {
  color: var(--color-primary);
}

.logo--highlight {
  color: var(--color-primary);
}

.header__nav {
  display: flex;
  align-items: center;
  gap: 1.25rem;
  flex: 1 1 auto; /* let nav take remaining width so items are fully visible */
  justify-content: center;
}

.header__nav a {
  color: var(--color-text);
  font-weight: 700;
  font-size: 1.05rem;
  transition: color var(--transition-fast);
  position: relative;
  letter-spacing: 0.01em;
}

.header__nav a:hover {
  color: var(--color-primary);
}

.header__nav a::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--color-primary);
  transition: width var(--transition-fast);
}

.header__nav a:hover::after {
  width: 100%;
}

.header__book {
  background: linear-gradient(135deg, var(--color-primary), color-mix(in oklab, var(--color-primary) 82%, #ffffff 18%));
  color: var(--color-on-primary);
  padding: 0.7rem 1.25rem;
  border-radius: 999px;
  font-weight: 700;
  transition: transform var(--transition-fast), box-shadow var(--transition-fast), background var(--transition-fast);
  border: 2px solid color-mix(in oklab, var(--color-primary) 85%, #0000 15%);
  box-shadow: 0 6px 18px var(--color-shadow);
}

.header__book:hover {
  background: linear-gradient(135deg, var(--color-primary-hover), var(--color-primary));
  color: var(--color-on-primary);
  transform: translateY(-2px);
  box-shadow: 0 6px 18px var(--color-shadow);
}

.header__nav .header__book,
.header__nav .header__book:visited {
  color: var(--color-on-primary);
}

.header__nav .header__book:hover,
.header__nav .header__book:focus {
  color: var(--color-on-primary);
}

.header__nav .header__book::after {
  display: none;
}

/* Mobile nav toggle (hidden on desktop) */
.header__menu-toggle {
  display: none;
  background: transparent;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.header__menu-toggle .menu-icon {
  width: 22px;
  height: 2px;
  background: var(--color-text);
  position: relative;
  display: inline-block;
}
.header__menu-toggle .menu-icon::before,
.header__menu-toggle .menu-icon::after {
  content: '';
  position: absolute;
  left: 0;
  width: 22px;
  height: 2px;
  background: var(--color-text);
}
.header__menu-toggle .menu-icon::before { top: -7px; }
.header__menu-toggle .menu-icon::after { top: 7px; }

/* Slide-out menu */
.mobile-menu {
  position: fixed;
  inset: 0; /* top/right/bottom/left: 0 relative to viewport */
  width: 100%; /* full-screen overlay to catch outside clicks */
  max-width: none;
  background: rgba(0, 0, 0, 0.4); /* dark overlay to dim background */
  border-left: none;
  box-shadow: none;
  transition: opacity var(--transition-medium);
  z-index: 10000; /* above header and any content */
  height: 100vh; /* ensure explicit height for children to inherit */
  opacity: 0;
  pointer-events: none;
}
.mobile-menu.open { opacity: 1; pointer-events: auto; }
.mobile-menu__panel {
  position: relative;
  height: 100%;
  background: var(--color-background);
  border-left: 1px solid var(--color-border);
  box-shadow: -8px 0 24px var(--color-shadow);
  width: 70%;
  max-width: 300px;
  margin-left: auto; /* anchor to right inside full-screen overlay */
  transform: translateX(100%);
  transition: transform var(--transition-medium);
  will-change: transform;
  pointer-events: auto;
}
.mobile-menu.open .mobile-menu__panel { transform: translateX(0); }
.mobile-menu__content {
  display: flex;
  flex-direction: column;
  gap: .25rem;
  padding: 80px 1.25rem 1.5rem; /* leave room for close button and sticky header */
  height: 100%;
  overflow-y: auto;
}
.mobile-menu__content a {
  color: var(--color-text);
  font-weight: 700;
  font-size: 1.35rem;
  padding: 0.85rem 0.75rem;
  border-radius: 8px;
  border: 1px solid transparent;
  transition:
    background-color var(--transition-fast),
    border-color var(--transition-fast),
    transform var(--transition-fast);
}
.mobile-menu__cta { margin-top: 0.5rem; }
.menu-backdrop { position: fixed; inset: 0; background: rgba(0, 0, 0, 0.3); z-index: 9998; }

.mobile-menu__content a:hover {
  background: var(--color-surface);
  border-color: var(--color-border);
  transform: translateX(2px);
}
.mobile-menu__burger {
  position: absolute;
  top: 12px;
  left: 12px;
  width: 40px;
  height: 40px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  color: var(--color-text);
  z-index: 1;
}

.mobile-menu__theme-toggle { position: absolute; top: 12px; right: 12px; z-index: 1; }
.mobile-menu__burger .menu-icon { width: 22px; height: 2px; background: var(--color-text); position: relative; display: inline-block; }
.mobile-menu__burger .menu-icon::before,
.mobile-menu__burger .menu-icon::after { content: ''; position: absolute; left: 0; width: 22px; height: 2px; background: var(--color-text); }
.mobile-menu__burger .menu-icon::before { top: -7px; }
.mobile-menu__burger .menu-icon::after { top: 7px; }
.mobile-menu__burger:hover { filter: brightness(1.05); }

.header__actions { display: inline-flex; align-items: center; gap: 0.85rem; margin-left: auto; }
.header__phone { display: inline-flex; align-items: center; gap: 0.35rem; color: var(--color-text); font-weight: 600; }
.header__phone:hover { color: var(--color-primary); }
.phone-icon { font-size: 1.1rem; }
.phone-text { display: inline; }

.mobile-menu__cta { text-align: center; }

/* Ensure CTA text readable inside drawer in light mode */
.mobile-menu .btn { background-color: var(--color-primary); color: var(--color-on-primary); border: 2px solid transparent; }
.mobile-menu .btn:hover { background-color: var(--color-primary-hover); color: var(--color-on-primary); }

/* Responsive design */
@media (max-width: 1250px) {
  .header { position: sticky; top: 0; z-index: 1000; background-color: var(--color-background); border-bottom: 1px solid var(--color-border); box-shadow: 0 2px 10px var(--color-shadow); backdrop-filter: saturate(180%) blur(6px); -webkit-backdrop-filter: saturate(180%) blur(6px); }
  .header__container { padding: 1rem; flex-direction: column; gap: 1rem; }
  .header__nav { gap: 1rem; flex-wrap: wrap; justify-content: center; }
  .header__logo-image { height: 60px; }
  .header__logo-text { display: none; }
  .header__flag { }
}

/* Responsive: show hamburger and hide desktop nav on small screens */
@media (max-width: 1250px) {
  .header { position: sticky; top: 0; z-index: 1000; background-color: var(--color-background); border-bottom: 1px solid var(--color-border); box-shadow: 0 2px 10px var(--color-shadow); backdrop-filter: saturate(180%) blur(6px); -webkit-backdrop-filter: saturate(180%) blur(6px); }
  .header__nav { display: none; }
  .header__actions { display: inline-flex; margin-left: auto; }
  .phone-text { display: none; }
  .header__phone { display: none; }
  .header__book { display: none; }
  .header__actions .theme-toggle { display: none; }
  .header__menu-toggle { display: inline-flex; }
  .header__container { padding: 0.75rem 1rem; display: flex; flex-direction: row; align-items: center; justify-content: flex-start; gap: 0.75rem; flex-wrap: nowrap; }
  .header__logo { flex: 1 1 auto; min-width: 0; display: flex; align-items: center; gap: 0.5rem; }
  .header__logo-text { display: inline; font-size: 1.8rem; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }
  .header__logo-image { height: 40px; flex: none; }
}

/* Make the drawer text a bit smaller on mobile */
@media (max-width: 768px) {
  .mobile-menu__content a { font-size: 1.1rem; padding: 0.7rem 0.6rem; }
  .mobile-menu__content { gap: 0.9rem; padding: 72px 1rem 1.25rem; }
  .mobile-menu__panel { max-width: 280px; }
  .mobile-menu .btn.mobile-menu__cta { font-size: 0.95rem; padding: 0.6rem 0.9rem; min-height: 42px; }
}
